.services {
  margin-bottom: 70px;
  &__description {
    text-align: center;
  }

  &__list {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;

    @media (min-width: $desktop-width) {
      flex-direction: row;
      flex-wrap: wrap;
    }
  }
}
