*,
*:before,
*:after {
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.wrapper {
  display: flex;
  min-height: 100%;
  flex-direction: column;
}

.container {
  box-sizing: border-box;
  margin: 0 auto;
  width: 430px;
  padding: 0 20px;
  font-family: "Space Grotesk", "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
  color: black;
  font-weight: 400;
}
@media (min-width: 1440px) {
  .container {
    width: 1440px;
    padding: 0 110px;
    font-size: 18px;
    line-height: 27px;
    color: black;
    font-weight: 400;
  }
}

@font-face {
  font-family: "Space Grotesk";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Space Grotesk"), url("../../fonts/SpaceGrotesk-Regular.woff2") format("woff2"), url("../../fonts/SpaceGrotesk-Regular.woff") format("woff");
}
@font-face {
  font-family: "Space Grotesk";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local("Space Grotesk"), url("../../fonts/SpaceGrotesk-Medium.woff2") format("woff2"), url("../../fonts/SpaceGrotesk-Medium.woff") format("woff");
}
.button {
  width: 100%;
  height: 68px;
  color: white;
  background-color: black;
  border-radius: 14px;
  cursor: pointer;
  font-size: 18px;
  line-height: 28px;
}

.main-heading {
  font-size: 36px;
  line-height: 54px;
  color: black;
  font-weight: 400;
  background-color: #B9FF66;
  width: fit-content;
  padding: 0 10px;
  margin: 0 auto;
  margin-bottom: 30px;
  border-radius: 7px;
  font-weight: 500;
}

.main-description {
  padding-bottom: 40px;
  text-align: left;
}

.page-header {
  padding: 30px 0;
}
@media (min-width: 1440px) {
  .page-header {
    padding: 60px 0 70px 0;
  }
}

.main-nav {
  position: relative;
}
@media (min-width: 1440px) {
  .main-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: auto;
  }
}

.main-nav__logo {
  width: 144px;
  height: 23.61px;
}
@media (min-width: 1440px) {
  .main-nav__logo {
    width: 219.54px;
    height: 36px;
  }
}

.main-nav__list {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
  gap: 10px;
}
@media (min-width: 1440px) {
  .main-nav__list {
    flex-direction: row;
    gap: 20px;
  }
}

.main-nav__toogle {
  display: block;
  position: absolute;
  width: 24px;
  height: 24px;
  top: 5px;
  right: 0;
  z-index: 2;
  border: 0;
  cursor: pointer;
  color: black;
  background-color: transparent;
}
@media (min-width: 1440px) {
  .main-nav__toogle {
    display: none;
  }
}

@media (max-width: 1439px) {
  .main-nav--opened {
    padding-top: 13px;
  }
}

.main-nav--opened .main-nav__toogle::before,
.main-nav--opened .main-nav__toogle::after {
  content: "";
  position: absolute;
  top: 27px;
  right: 0;
  width: 24px;
  height: 2px;
  background-color: currentColor;
}

.main-nav--opened .main-nav__toogle::before {
  transform: translateY(-50%) rotate(45deg);
}

.main-nav--opened .main-nav__toogle::after {
  transform: translateY(-50%) rotate(-45deg);
}

.main-nav--closed .main-nav__toogle::before {
  content: "";
  position: absolute;
  top: 5px;
  right: 0;
  width: 24px;
  height: 1.5px;
  background-color: black;
  box-shadow: 0 8px 0 0 #000, 0 16px 0 0 #000;
}

@media (max-width: 1439px) {
  .main-nav--closed .main-nav__wrapper {
    display: none;
  }
  .main-nav--nojs .main-nav__wrapper {
    display: block;
  }
  .main-nav--nojs .main-nav__toogle {
    display: none;
  }
}
.site-list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.site-list__item a {
  text-decoration: none;
  color: inherit;
  padding: 5px 0;
}
@media (min-width: ${desktop-width}) {
  .site-list__item--link {
    padding: 20px 35px;
    border: 1px solid black;
    border-radius: 14px;
  }
}

.promo__heading {
  background-color: transparent;
}

@keyframes animMarquee {
  0% {
    transform: translateX(50%);
  }
  100% {
    transform: translateX(-100%);
  }
}
@keyframes animLeftMarquee {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(50%);
  }
}
.partners {
  padding: 60px 0;
  overflow: hidden;
}
.partners__list {
  width: max-content;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  column-gap: 30px;
  flex-direction: row;
}
.partners__list--first {
  animation: animMarquee 15s linear infinite;
  padding-bottom: 10px;
}
@media (min-width: 1440px) {
  .partners__list--first {
    animation: none;
  }
}
.partners__list--second {
  animation: animLeftMarquee 15s linear infinite;
}
@media (min-width: 1440px) {
  .partners__list--mobile {
    display: none;
  }
}

.partners__item img {
  filter: saturate(0);
}
.partners__item img:hover {
  filter: none;
}

.services {
  margin-bottom: 70px;
}
.services__description {
  text-align: center;
}
.services__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
}
@media (min-width: 1440px) {
  .services__list {
    flex-direction: row;
    flex-wrap: wrap;
  }
}

.services-list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.services-list__item, .services-list__item:nth-of-type(4n) {
  padding: 50px;
  width: 390px;
  min-height: 322px;
  background-color: #F3F3F3;
  border: 1px solid black;
  box-shadow: inset 0 -7px 0;
  border-radius: 45px;
  position: relative;
}
.services-list__item:nth-of-type(3n-1) {
  background-color: #B9FF66;
}
.services-list__item:nth-of-type(3n) {
  background-color: black;
}
.services-list__link {
  cursor: pointer;
  display: block;
  position: absolute;
  bottom: 57px;
  width: 41px;
  height: 41px;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-image: url("../../img/icons/icon-arrow-green-and-black.svg");
}
.services-list__item::after {
  content: "";
  display: block;
  width: 165px;
  height: 129px;
  position: absolute;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-image: url("../../img/services/tokyo-magnifier-web-search-with-elements.png");
  bottom: 50px;
  right: 50px;
}
.services-list__item:nth-of-type(2)::after {
  background-image: url("../../img/services/tokyo-selecting-a-value-in-the-browser-window.png");
}
.services-list__item:nth-of-type(3)::after {
  width: 127.12px;
  height: 129px;
  background-image: url("../../img/services/tokyo-browser-window-with-emoticon-likes-and-stars-around.png");
}
.services-list__item:nth-of-type(4)::after {
  width: 155px;
  height: 142px;
  background-image: url("../../img/services/tokyo-sending-messages-from-one-place-to-another.png");
}
.services-list__item:nth-of-type(5)::after {
  width: 138.72px;
  height: 129px;
  background-image: url("../../img/services/tokyo-many-browser-windows-with-different-information.png");
}
.services-list__item:nth-of-type(6)::after {
  width: 157.78px;
  height: 129px;
  background-image: url("../../img/services/tokyo-volumetric-analytics-of-different-types-in-web-browsers.png");
}
.services-list__item:nth-of-type(3n) .services-list__link {
  background-image: url("../../img/icons/icon-arrow-black.svg");
}
@media (max-width: 1439px) {
  .services-list__link span {
    display: none;
  }
}
.services-list__heading {
  font-size: 26px;
  line-height: 39px;
  color: black;
  font-weight: 500;
  background-color: white;
  border-radius: 7px;
  width: fit-content;
  padding: 0 5px;
}
.services-list__item:nth-of-type(1) .services-list__heading, .services-list__item:nth-of-type(4n) .services-list__heading {
  background-color: #B9FF66;
}

.invitation {
  width: 390px;
  height: 370px;
  background-color: #F3F3F3;
  border-radius: 45px;
  padding: 50px;
  margin-bottom: 60px;
}

.ivitation__description {
  padding-bottom: 20px;
}

.ivitation__heading {
  font-size: 26px;
  line-height: 30px;
  color: black;
  font-weight: 500;
  padding-bottom: 20px;
}

@media (max-width: 1439px) {
  .ivitation__button span {
    display: none;
  }
}

.cases {
  overflow: hidden;
  margin-bottom: 60px;
}
.cases__description {
  text-align: center;
}
.cases__list {
  display: flex;
  gap: 20px;
}

.cases-list {
  margin: 0;
  padding: 0;
  list-style: none;
  width: fit-content;
}
.cases-list__item {
  width: 350px;
  height: 252px;
  background-color: black;
  color: white;
  padding: 42px 50px;
  border-radius: 45px;
}
.cases-list__description {
  padding-bottom: 20px;
}
.cases-list__link {
  position: relative;
  cursor: pointer;
  text-decoration: none;
  color: inherit;
  font-size: 20px;
  line-height: 28px;
  color: #B9FF66;
  font-weight: 400;
}
.cases-list__link::after {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  right: -40px;
  top: 5px;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-image: url("../../img/arrow-green.svg");
}

.working-process {
  margin-bottom: 60px;
}
.working-process__heading {
  font-size: 36px;
  line-height: 45px;
  color: black;
  font-weight: 500;
}
.working-process__description {
  text-align: center;
}
.working-process__list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.process-list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.process-list__item {
  width: 100%;
  min-height: 98px;
  background-color: #F3F3F3;
  border-radius: 45px;
  border: 1px solid black;
  box-shadow: inset 0 -7px 0;
  padding: 30px 30px;
  display: flex;
  flex-direction: column;
}
.process-list__item--opened {
  height: fit-content;
  background-color: #B9FF66;
}
.process-list__item div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 71px;
  position: relative;
}
.process-list__item div::before {
  content: "01";
  font-size: 30px;
  line-height: 30px;
  color: black;
  font-weight: 500;
  position: absolute;
  width: 38px;
  height: 38px;
  left: 0px;
  top: 0px;
}
.process-list__item:nth-child(2) div::before {
  content: "02";
  top: 10px;
}
.process-list__item:nth-child(3) div::before {
  content: "03";
}
.process-list__item:nth-child(4) div::before {
  content: "04";
  top: 10px;
}
.process-list__item:nth-child(5) div::before {
  content: "05";
  top: 10px;
}
.process-list__item:nth-child(6) div::before {
  content: "06";
}
.process-list__item--opened div {
  padding-bottom: 20px;
}
.process-list__item--opened div::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: black;
  bottom: 0;
  left: 0;
}
.process-list__heading {
  font-size: 18px;
  line-height: 27px;
  color: black;
  font-weight: 500;
  max-width: 203px;
}
.process-list__button {
  width: 31px;
  height: 31px;
  cursor: pointer;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-image: url("../../img/icons/icon-plus.png");
  background-size: contain;
}
.process-list__item--opened .process-list__button {
  background-image: url("../../img/icons/icon-minus.png");
}
.process-list__description {
  display: none;
}
.process-list__item--opened .process-list__description {
  display: block;
  padding-top: 20px;
  padding-bottom: 0;
}

.team {
  padding-bottom: 60px;
}
.team__description {
  text-align: center;
}
.team__list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.team__button {
  margin-top: 35px;
}
.team__button--clicked {
  display: none;
}

.team-list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.team-list__item {
  width: 100%;
  min-height: 295px;
  border-radius: 45px;
  border: 1px solid black;
  box-shadow: inset 0 -7px 0;
  padding: 40px 50px;
  display: flex;
  flex-direction: column;
}
.team-list__item div {
  display: flex;
  flex-direction: row;
  position: relative;
  align-items: center;
  padding-bottom: 20px;
}
.team-list__item div::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: black;
  bottom: 0;
  left: 0;
}
.team-list__link {
  width: 34px;
  height: 34px;
  cursor: pointer;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url("../../img/icons/icon-social.png");
  position: absolute;
  top: 0;
  right: 0;
}
.team-list__person {
  padding-left: 20px;
}
.team-list__name {
  font-size: 18px;
  line-height: auto;
  color: black;
  font-weight: 500;
}
.team-list__description {
  padding-top: 20px;
  padding-bottom: 0;
}
.team-list--collapsed .team-list__item:nth-child(5), .team-list--collapsed .team-list__item:nth-child(6) {
  display: none;
}
.team-list:not(.team-list--collapsed) .team-list__item:nth-child(5), .team-list:not(.team-list--collapsed) .team-list__item:nth-child(6) {
  display: block;
}

.testimonials {
  margin-bottom: 60px;
  overflow: visible;
}
.testimonials__description {
  text-align: center;
}
.testimonials__slider {
  width: 100%;
  background-color: #191A23;
  color: white;
  border-radius: 45px;
  padding: 30px 30px 60px 30px;
}
.testimonials__slider-list {
  display: flex;
  gap: 60px;
}

.slider-list {
  margin: 0;
  padding: 0;
  list-style: none;
  width: fit-content;
  margin-bottom: 60px;
}
.slider-list__item {
  display: flex;
  flex-direction: column;
  gap: 20px;
  background-color: #191A23;
}
.slider-list__container {
  width: 330px;
  min-height: 328px;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: auto auto;
  background-image: url("../../img/testimonials/testimonials-union.svg");
}
.slider-list__description {
  padding: 30px;
}
.slider-list__person {
  align-self: flex-end;
}
.slider-list__name {
  font-size: 18px;
  line-height: auto;
  color: #B9FF66;
  font-weight: 500;
}